import React, { useState } from "react";
import { useSelector } from "react-redux";
import { Divider, Tabs, Tab, Alert } from "@mui/material";
import "./TeamPrompt.css";
import styled from "@emotion/styled";
import Dogtag from "./Dogtag";
import ProfileInfo from "./ProfileInfo";
import Notification from "../Notification";

const StyledTabs = styled(Tabs)({
  "& .MuiTabs-indicator": {
    background: "#0FABFF",
  },
  margin: 0,
});

const StyledTab = styled(Tab)({
  minWidth: 0,
  width: "50%",
  textTransform: "none",
  fontSize: 13,
  fontWeight: 500,
  color: "white",
  opacity: ".8",
  "&.Mui-selected": {
    color: "white",
    opacity: 1,
  },
});

const StyledDivider = styled(Divider)({
  position: "absolute",
  margin: 0,
  right: 0,
  left: 0,
  bottom: 0,
  background: "rgba(255,255,255,.68)",
});

function TeamPrompt({
  currentFunction,
  name,
  handleProfileChange,
  profile,
  isReadOnly,
  readOnlyWarning,
}) {
  const currentSchedule = useSelector((state) => state.currentSchedule);
  const { timeslots, notScheduled } = currentSchedule;
  const workblocks = timeslots.map((ts) => currentSchedule.toWorkBlock(ts));
  const [currentTab, setCurrentTab] = useState("notScheduled");

  return (
    <div
      className="team-prompt hidden"
      style={{ display: currentFunction === name ? "flex" : "none" }}
    >
      {profile ? (
        <ProfileInfo profile={profile} />
      ) : (
        <>
          <div style={{ position: "relative" }}>
            <StyledTabs
              value={currentTab}
              onChange={(e, newTab) => setCurrentTab(newTab)}
            >
              <StyledTab label="Scheduled" value={"scheduled"} />
              <StyledTab label="The Bench" value={"notScheduled"} />
              <StyledDivider />
            </StyledTabs>
          </div>

          <ul
            className="scheduled"
            style={{ display: currentTab === "scheduled" ? "flex" : "none" }}
          >
            {workblocks.map(({ startTime, endTime, user }, index) => {
              return (
                <li key={index}>
                  <Dogtag
                    startTime={startTime}
                    endTime={endTime}
                    user={user}
                    handleProfileChange={handleProfileChange}
                  />
                </li>
              );
            })}
          </ul>

          <ul
            className="not-scheduled"
            style={{ display: currentTab === "notScheduled" ? "flex" : "none" }}
          >
            {notScheduled.map((user, index) => {
              return (
                <li key={index}>
                  <Dogtag
                    isReadOnly={isReadOnly}
                    readOnlyWarning={readOnlyWarning}
                    user={user}
                    handleProfileChange={handleProfileChange}
                  />
                </li>
              );
            })}
          </ul>
        </>
      )}
    </div>
  );
}

export default TeamPrompt;
